body {
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #f8f8f8;
  }

  p {
    color: #c4c4c4;
  }

  h1 {
    margin-bottom: 1em;
    border-bottom: 1px dotted #c4c4c4;
    font-size: 2.2em; 
  }
  h2 {
    font-size: 1.6em;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }